.fox {
  position: absolute;
  background: transparent; 
  top: 3px;
  right: 3px;
  border-radius: 12px;
  padding: 8px; 
  color: white; 
  border-width: 1px;
  border: 1px solid hsla(55, 78%, 74%, 1);
}
.fox:hover {
  background: rgba(255, 255, 255, 0.2);
  cursor: pointer; 
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  border-radius: 12px;
  padding: 8px; 
}

